import React from 'react'
import {Icon, Upload, message, BackTop, Spin, Badge, Tag} from 'antd'
import CustomBreadcrumb from '../../components/CustomBreadcrumb'

import {isAuthenticated} from "../../utils/Session";
import ApiUtil from "../../utils/ApiUtil";
import HttpUtil from "../../utils/HttpUtil";


import {getfilemd5sum} from "../../utils/utils";
import UserInfoCard from "../../components/UserInfoCard";
import './style.css'

const Dragger = Upload.Dragger;


function deleteFile(filename, fileList) {
    //
    let index = -1;
    for (let i = 0; i < fileList.length; i++) {
        if (fileList[i] === filename) index = i;
    }
    fileList.splice(index, 1);
    return fileList;
}

const props2 = {
    name: 'file',
    multiple: true,
    onChange(info) {

        if (info.file.status === 'done') {

            message.success({content: `${info.file.name} `, key});
        } else if (info.file.status === 'error') {
            message.error({content: `${info.file.name} `, key});
        } else if (info.file.status === 'removed') {
            //message.warn(`${info.file.name} `);
            HttpUtil.get(`${ApiUtil.API_FILE_DELETE_BY_MD5}/${info.file.md5}`)
                .then(
                    re => {
                        message.info(re.data.code === 0 ? '' : '');
                    }
                ).catch(error => {
                message.error(error.message);
            });
        }

    },
    showUploadList: {

        showRemoveIcon: true,

    },
};
const key = 'updatable'

class UploadDemo extends React.Component {
    constructor() {

        super();

        this.state = {
            visible: false,
            avatar: require('../../assets/img/04.jpg')
        }
        this.beforeUpload = this.beforeUpload.bind(this)
        //this.click = this.click.bind(this); //,,
        this.toggleLoading = this.toggleLoading.bind(this)
    }

    toggleLoading() {
        this.setState({visible: !this.state.visible})
    }

    beforeUpload(file, fileList) {


        message.loading({content: ",.......", key, duration: 0})
        const promise = new Promise(function (reslove, reject) {

            message.loading({content: "", key, duration: 0});

            const isVIDEO = file.type === 'video/mp4'
            if (!isVIDEO) {
                message.error({content: "", key, duration: 2});
                fileList = this.deleteFile(file.name, fileList);
                return false;
            }
            message.loading({content: "", key, duration: 0});
            const isLt200M = file.size / 1024 / 1024 < 20;
            if (!isLt200M) {

                message.error({content: "15MB", key, duration: 2});
                fileList = this.deleteFile(file.name, fileList);
                return false;
            }
            message.loading({content: "md5", key, duration: 0});

            const reader = new FileReader();
            reader.readAsDataURL(file);
            let filemd5;

            getfilemd5sum(file).then(value => {
                filemd5 = value
                file.md5 = filemd5
                HttpUtil.get(ApiUtil.API_CHECK_FILE_MD5 + filemd5)
                    .then(
                        re => {

                            if (re.code === 1) {
                                //MD5
                                message.error({content: '', key})
                                fileList = deleteFile(file.name, fileList);
                                reject()
                            } else {
                                message.success({content: ` ${file.name} `, key, duration: 0})
                                reslove(file)
                            }

                        }
                    ).catch(error => {
                    reject()
                    message.error(error.message);
                });
            }).catch(error => {
                reject()
                message.error(error.message)
            })

        })
        return promise

    }


    render() {

        return (
            <div>
                <CustomBreadcrumb arr={['']}/>
                <div className={'outer'}>
                    <div className={'sider'}>
                        <UserInfoCard name={isAuthenticated()}/>
                    </div>
                    <div className={'content'}>
                        <div style={
                            {
                                background: "white",
                                borderRadius: "15px",
                                boxShadow: "0px 0px 20px rgba(137,137,137, 0.1)",
                                marginTop: "20px",
                                marginBottom: "50px",
                                padding: "20px"
                            }}>
                            <div style={{
                                fontWeight: "bold",
                                marginLeft: "30px",
                                marginBottom: "20px",
                                fontSize: "18px"
                            }}>
                            </div>
                            <div style={{marginLeft: "60px"}}>

                                <div style={{marginTop: 10}}>
                                    <Badge status="success"/><b></b>
                                </div>
                                <div style={{marginTop: 10}}><Badge status="success"/></div>
                                <div style={{marginTop: 10}}><Badge status="success"/></div>
                                <div style={{marginTop: 10}}><Badge status="success"/>
                                    ,
                                    <Tag color="#2db7f5">mp4</Tag>
                                    <Tag color="#2db7f5">rmvb</Tag>
                                    <Tag color="#2db7f5">avi</Tag>
                                    <Tag color="#2db7f5">mov</Tag>
                                </div>
                                <div style={{marginTop: 10}}><Badge status="success"/> <Tag
                                    color="#f50">15MB</Tag></div>
                            </div>
                        </div>

                        <Spin tip='' spinning={this.state.visible}>
                            <div style={
                                {
                                    background: "white",
                                    borderRadius: "15px",
                                    boxShadow: "0px 0px 20px rgba(137,137,137, 0.1)",
                                    marginTop: "20px",
                                    marginBottom: "50px",
                                    padding: "20px",
                                    height: '330px'
                                }}>
                                <div style={{
                                    fontWeight: "bold",
                                    marginLeft: "30px",
                                    marginBottom: "20px",
                                    fontSize: "18px"
                                }}>
                                </div>
                                <div style={{height: 230}}>
                                    <Dragger action={ApiUtil.API_FILE_UPLOAD + isAuthenticated()}
                                             beforeUpload={this.beforeUpload.bind(this)} {...props2}>
                                        <p className="ant-upload-drag-icon">
                                            <Icon type="inbox"/>
                                        </p>
                                        <p className="ant-upload-text"></p>
                                        <p className="ant-upload-hint"></p>
                                    </Dragger>
                                </div>

                            </div>
                        </Spin>
                    </div>
                </div>


                <BackTop visibilityHeight={200} style={{right: 50}}/>
            </div>
        )
    }
}


export default UploadDemo



